import React from 'react';
import Link from 'gatsby-link';
import '../css/index.css';

class Template extends React.Component {
  render() {
    const { location, children } = this.props;
    const isHome = location.pathname === '/';
    const logoInner = (
      <Link to={'/'}>
        <svg
          className="site-name__image"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 71 100"
          width="60"
          role="presentation"
        >
          <path
            className="site-name__horns"
            d="M58.627 56.034l-10.35-27.41s15.944-1.4 13.425-13.706C59.637 4.81 53.592 1.56 52.286.978 50.978.393 51.634.27 51.634.27s9.754.433 13.985 7.376c4.474 7.343 8.39 20.698-.56 32.446-8.95 11.747-6.433 15.942-6.433 15.942zM12.362 56.034l10.35-27.41s-15.944-1.4-13.428-13.707C11.352 4.81 17.398 1.56 18.704.977c1.304-.584.65-.707.65-.707S9.6.703 5.37 7.646c-4.476 7.342-8.392 20.698.56 32.446 8.95 11.747 6.432 15.942 6.432 15.942z"
          />
          <path
            className="site-name__letter"
            d="M3.228 99.712l12.75-5.092 19.26-56.215 19.51 56.035 12.62 5.272L39.86 25.326h-9.188"
          />
        </svg>
        <span className="site-name__text">
          Devilish Alchemist's <span>Blog</span>
        </span>
      </Link>
    );

    return <div className="sections-wrap">
        <header className="site-header">
          {isHome ? <h1 className="site-name">
              {logoInner}
            </h1> : <p className="site-name">{logoInner}</p>}
          <p className="site-description">Front-end musings and stuff</p>
        </header>
        <main className="section-main">{children()}</main>
        <footer className="site-footer">
          <p>
            Written by <a href="mailto:marek@devalchemist.com">Marek</a>, of <a href="https://devalchemist.com">
              Devilish Alchemist
            </a> fame. Not actually famous.
          </p>
        </footer>
      </div>;
  }
}

Template.propTypes = {
  children: React.PropTypes.func,
  location: React.PropTypes.object,
  route: React.PropTypes.object
};

export default Template;



